import CarsStatistics from '../components/statistics/CarsPieStatistics';
import CarsBarStatistics from '../components/statistics/CarsBarStatistics';
import CarsBarTimesStatistics from '../components/statistics/CarsBarTimesStatistics';
import CarsBarRevenueStatistics from '../components/statistics/CarsBarRevenueStatistics';
import StatisticsCardsComponent from '../components/statistics/StatisticsCardsComponent';

const Car = () => {
    return (
        <div className="mt-6 w-full">
            <StatisticsCardsComponent />
            <div className="mt-6 grid custom-grid-big gap-6 justify-center sm:justify-between">
                <CarsStatistics />
                <CarsBarStatistics />
                <CarsBarTimesStatistics />
                <CarsBarRevenueStatistics />
            </div>
        </div>
    );
};

export default Car;
