import { Bar, BarChart, CartesianGrid, XAxis, YAxis } from 'recharts';

import { ChartConfig, ChartContainer, ChartTooltip, ChartTooltipContent } from '@/components/ui/chart';

const chartData = [
    { browser: 'chrome', visitors: 187, fill: 'var(--color-chrome)' },
    { browser: 'safari', visitors: 200, fill: 'var(--color-safari)' },
    { browser: 'firefox', visitors: 275, fill: 'var(--color-firefox)' },
];

const chartConfig = {
    visitors: {
        label: 'Visitors',
    },
    chrome: {
        label: 'تكاليف الصيانة',
        color: 'hsl(var(--yellow-warning-500))',
    },
    safari: {
        label: 'تكاليف الوقود',
        color: 'hsl(var(--chart-2))',
    },
    firefox: {
        label: 'تكاليف التأمين',
        color: 'hsl(var(--chart-3))',
    },
    edge: {
        label: 'Edge',
        color: 'hsl(var(--chart-4))',
    },
    other: {
        label: 'Other',
        color: 'hsl(var(--chart-5))',
    },
} satisfies ChartConfig;

interface Props {
    barLabelColor?: string;
}

export function BarChartComponent({ barLabelColor = '#fff' }: Props) {
    return (
        <div className="min-w-[508px] min-h-[260px]">
            {/* <CardContent> */}
            <ChartContainer config={chartConfig}>
                <BarChart
                    margin={{
                        top: 0,
                        right: 0,
                        left: 0,
                        bottom: 0,
                    }}
                    accessibilityLayer
                    data={chartData}
                    barCategoryGap={105}
                >
                    <CartesianGrid vertical={false} strokeDasharray="5 5" />
                    <XAxis
                        dataKey="browser"
                        tickLine={false}
                        tickMargin={10}
                        axisLine={true}
                        tickFormatter={(value) => chartConfig[value as keyof typeof chartConfig]?.label}
                        // width={600}
                        tick={{ fill: '#000000B2' }}
                    />
                    {/* <ChartTooltip content={<ChartTooltipContent hideLabel />} />
                        <ChartLegend content={<ChartLegendContent />} /> */}
                    <YAxis
                        axisLine={true}
                        tickLine={false}
                        tick={{ fill: '#6b7280', textAnchor: 'start' }}
                        // width={40}
                    />
                    <ChartTooltip
                        formatter={(value) => value}
                        cursor={false}
                        content={<ChartTooltipContent hideLabel />}
                    />
                    <Bar
                        dataKey="visitors"
                        strokeWidth={2}
                        radius={[9999, 9999, 0, 0]}
                        barSize={68}
                        activeIndex={2}
                        background={{ fill: '#D6DBED', opacity: 0.4 }}
                        label={{
                            // Add labels to the bars
                            position: 'insideTop', // Position of the label (e.g., 'top', 'inside', 'insideStart', 'insideEnd')
                            fill: barLabelColor, // Text color
                            opacity: 0.7,
                            fontSize: 12, // Font size
                            fontWeight: 'bold', // Font weight
                        }}
                    />
                </BarChart>
            </ChartContainer>
            {/* </CardContent> */}
        </div>
    );
}
